:root {
  --primary-font: "Primary";
  --primary-font-italic: "Primary_italic", Arial, sans-serif;
  --primary-font-extralight: "Primary_extralight", Arial, sans-serif;
  --primary-font-light: "Primary_light", Arial, sans-serif;
  --primary-font-medium: "Primary_medium", Arial, sans-serif;
  --primary-font-semibold: "Primary_semibold", Arial, sans-serif;
  --primary-font-bold: "Primary_bold", Arial, sans-serif;
  --primary-font-black: "Primary_black", Arial, sans-serif;
  scroll-behavior: smooth;
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: var(--primary-font);
  background-color: hsl(0, 0%, 90%);
}

.container {
  width: 94%;
  margin: 0 auto;
}
.container > .navbar__overlay--active {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsla(0, 0%, 0%, 0.5);
  z-index: 9;
}

.section__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: hsla(0, 0%, 0%, 0.5);
  z-index: 9;
}

.section__title {
  margin: 1.6em 0;
  font-family: var(--primary-font-extralight);
  font-size: 2.8rem;
  text-transform: uppercase;
  text-align: center;
}

.navbar {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0.5em 0;
  background-color: hsl(0, 0%, 90%);
  z-index: 10;
}
.navbar__logo {
  margin-left: 0.5em;
  display: flex;
  align-items: center;
  gap: 0.25em;
  font-family: var(--primary-font-light);
  font-size: 3.2rem;
  text-decoration: none;
  color: hsl(0, 0%, 0%);
}
.navbar__logo-icon {
  width: 25px;
}
.navbar__burger-icon {
  position: relative;
  width: 72px;
  height: 66px;
  padding: 1em;
  background: none;
  border: 2px solid transparent;
  border-radius: 4px;
  font-size: 1.6rem;
  cursor: pointer;
}
.navbar__burger-icon-line, .navbar__burger-icon-line::before, .navbar__burger-icon-line::after {
  width: 37px;
  height: 4px;
  background-color: hsl(0, 0%, 0%);
  border-radius: 2px;
}
.navbar__burger-icon-line::before, .navbar__burger-icon-line::after {
  content: "";
  position: absolute;
  left: 16px;
  transition: transform 200ms ease-in-out;
}
.navbar__burger-icon-line::before {
  top: 16px;
}
.navbar__burger-icon-line::after {
  bottom: 16px;
}
.navbar__burger-icon--active > .navbar__burger-icon-line::before {
  transform: translateY(-4px);
}
.navbar__burger-icon--active > .navbar__burger-icon-line::after {
  transform: translateY(4px);
}
.navbar__burger-icon:focus {
  outline: none;
  border-color: hsl(0, 0%, 0%);
}
.navbar__links {
  display: none;
  flex-basis: 100%;
}
.navbar__links--active {
  display: block;
}
.navbar__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style-type: none;
}
.navbar__item {
  width: 100%;
  text-align: center;
}
.navbar__link {
  width: 100%;
  display: inline-block;
  padding: 2em 1em;
  text-decoration: none;
  color: hsl(0, 0%, 0%);
  transition: background-color 100ms linear, color 200ms linear;
}
@media (hover: hover) {
  .navbar__link:hover {
    background-color: hsl(0, 0%, 0%);
    color: hsl(0, 0%, 90%);
  }
}

.hero {
  position: relative;
  height: 25vh;
}
.hero__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: bottom;
}
.hero__text {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  color: hsl(0, 0%, 90%);
  z-index: 10;
}
.hero__title {
  margin-bottom: 0.5em;
  font-family: var(--primary-font-light);
}
.hero__description {
  font-size: 2rem;
}

.about__content {
  display: flex;
  flex-direction: column;
  gap: 3em;
}
.about__image-box {
  width: 100%;
  object-fit: cover;
}
.about__image {
  width: 100%;
}
.about__title {
  margin-bottom: 1em;
  text-align: center;
}
.about__description {
  color: hsl(0, 0%, 25%);
}

@media screen and (min-width: 992px) {
  .container {
    width: 960px;
  }
  .navbar__burger-icon {
    display: none;
  }
  .navbar__links {
    display: block;
    flex-basis: auto;
  }
  .navbar__list {
    flex-direction: row;
  }
  .hero__text {
    padding-left: 1em;
  }
  .hero__title {
    font-size: 2rem;
  }
  .hero__description {
    font-size: 3.2rem;
  }
  .about__content {
    flex-direction: row;
  }
  .about__content > * {
    flex-basis: 50%;
  }
  .about__title {
    text-align: initial;
  }
}